import * as React from 'react';
import { action } from 'mobx';
import { observer } from "mobx-react-lite";
import styled from '@emotion/styled';

import { Store, Sheet } from '../../store/store';
import { repLinGradient } from '../../utils';

const fontSize = 18;
const lineHeight = 1.5;

const Wrapper = styled.div`
    width: 1200px;
    /* margin: 0 auto; */
    font-family: 'Courier Prime', monospace;
    font-size: ${fontSize}px;
    line-height: ${lineHeight};
    position: relative;
`;

const lineNumberSidebarWidth = fontSize * (2 + 3) * .6;
const LineNumberSidebar = styled.div`
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    /* position: absolute; */
    left: 0;
    top: 0;
    background-color: #e7e7e7;
    width: ${lineNumberSidebarWidth}px;
    text-align: right;

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    li {
        padding: 0 ${fontSize * .6}px;
    }
`;
const TextAreaWrapper = styled.div`
    position: relative;
    /* overflow: auto; */
    height: 100vh;
    /* left: ${lineNumberSidebarWidth}px; */
`;
const EditorTextarea = styled.textarea`
    /* background: #f7f7f7; */
    /* 180deg fixes moving background depending on container height */
    background: ${repLinGradient(180, '#f7f7f7', '#fcfcfc', '1.5em', '3em')};
    white-space: pre;
    display: block;
    border: 0;
    outline: 0;
    padding: 0;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    color: transparent;
    caret-color: black;

    &::selection {
        background: rgba(127, 127, 255, .2);
        color: transparent;
    }
`;
const EditorOverlay = styled.div`
    /* display: none; */
    /* white-space: pre; */
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    font-family: inherit;
    font-size: inherit;
    pointer-events: none;

    * {
        pointer-events: none;
    }
`;
const EditorContentEditable = styled.div`
    background: #f7f7f7;
    white-space: pre;
    outline: none;
`;

// @todo use @emotion/babel-plugin
// Error: Component selectors can only be used in conjunction with @emotion/babel-plugin.
const EditorMetaLineControlsBlock = styled.div`
    display: none;
`;
const EditorMetaLine = styled.div`
    position: absolute;
    color: #aaa;
    display: flex;
    pointer-events: none;

    &:hover {
        .EditorMetaLineControlsBlock {
            display: block;
        }
    }
`;
const EditorMetaLineComputedText = styled.div`

`;

const TasksSideListBlock = styled.div`
    font-family: 'Courier Prime', monospace;
    font-size: 14px;
`;

export const SheetEditor: React.FunctionComponent<{ store: Store, sheet: Sheet }> = observer(({ store, sheet }) => {
    const [sourceFileContent, setSourceFileContent] = React.useState(sheet.fileContent);
    const [transformedFileContent, setTransformedFileContent] = React.useState(sheet.sheetLinesCollection.transformedFileContent);
    // const contentEditable = React.useRef<HTMLDivElement>(null);

    // React.useEffect(() => {
    //     refs.editor.selectionStart
    // }, [transformedFileContent]);

    React.useEffect(() => {
        setSourceFileContent(sheet.fileContent);
        setTransformedFileContent(sheet.sheetLinesCollection.transformedFileContent);
    }, [sheet]);

    const editorComputedHeight = sheet.lines.length * fontSize * lineHeight;

    // const longestLineLength = sheet.lines.reduce((max, line) => {
    //     return line.input.length > max ? line.input.length : max;
    // }, 0);

    // console.log('longestLineLength', longestLineLength);

    return (
        // <Wrapper style={{ width: longestLineLength * fontSize * .6}}>
        <Wrapper>
            {/* <LineNumberSidebar>
                <ul>
                    {
                        sheet.lines.map((l, i) => {
                            return (
                                <li key={i}>{i}</li>
                            )
                        })
                    }
                </ul>
            </LineNumberSidebar> */}
            <TextAreaWrapper
                style={{ height: editorComputedHeight + 'px' }}
            >
                <EditorTextarea
                    spellCheck={false}
                    value={sourceFileContent}
                    onChange={async e => {
                        // console.log(e.currentTarget.textContent);
                        // console.log(e.target.sele);
                        // const { textContent } = e.currentTarget;
                        // if (typeof textContent !== 'string') {
                        //     throw new Error(`textContent is no a string (${textContent})`);
                        // }
                        const newContent = e.currentTarget.value;

                        setSourceFileContent(newContent);
                        // sheet.updateFileContent(newContent);
                        sheet.updateFileContent(newContent);
                        setTransformedFileContent(sheet.sheetLinesCollection.transformedFileContent);

                        // store.saveStorage();
                        await store.updateSheet(sheet, {content: newContent});
                        // if (contentEditable.current) {
                        //     contentEditable.current.innerHTML = sheet.transformedFileContent;
                        // }
                    }}
                />
                <EditorOverlay dangerouslySetInnerHTML={{ __html: transformedFileContent }} />
            </TextAreaWrapper>
            {/* <EditorContentEditable
                // ref={contentEditable}
                contentEditable
                dangerouslySetInnerHTML={{ __html: transformedFileContent }}
                onInput={e => {
                    // console.log(e.currentTarget.textContent);
                    // console.log(e.target.sele);
                    const { textContent } = e.currentTarget;
                    if (typeof textContent !== 'string') {
                        throw new Error(`textContent is no a string (${textContent})`);
                    }
                    storage.save(textContent);
                    sheet.updateFileContent(textContent);
                    setTransformedFileContent(sheet.transformedFileContent);
                    // if (contentEditable.current) {
                    //     contentEditable.current.innerHTML = sheet.transformedFileContent;
                    // }
                }}
            /> */}
            {
                sheet.sheetLinesCollection.lines.map((l, i) => {
                    if (!l.nextDate || l.dateDiff === undefined) return '';
                    const lineStyle = {
                        left: `${fontSize * .6 * (l.input.length + 1)}px`,
                        top: `${fontSize * lineHeight * i}px`,
                    };
                    const delayStyle = {
                        ...(l.isComment === false ? {color: l.delayColor} : {}),
                    };
                    const dateDiffStr = `${(l.dateDiff && l.dateDiff > 0) ? '+' : ''}${l.dateDiff}`;
                    return (
                        <EditorMetaLine
                            key={`line-${i}`}
                            style={lineStyle}
                        >
                            <EditorMetaLineComputedText>→ {l.nextDate} (<span style={delayStyle}>{dateDiffStr}</span>)</EditorMetaLineComputedText>
                            {/* <EditorMetaLineControlsBlock className="EditorMetaLineControlsBlock">
                                <button onClick={() => {
                                    // @todo replace date
                                }}>mark done today</button>
                            </EditorMetaLineControlsBlock> */}
                        </EditorMetaLine>
                    );
                })
            }
        </Wrapper>
    )
});