import * as React from 'react';
import { action } from 'mobx';
import { observer } from "mobx-react-lite";
import styled from '@emotion/styled';

import { Store } from '../../store/store';
import { SheetView } from '../sheet-view/sheet-view';
import { SheetHeader } from '../sheet-view/sheet-header/sheet-header';
import { SheetEditor } from '../sheet-view/sheet-editor';
import { SheetTasksListBlock } from '../sheet-view/sheet-tasks-list-block';
import { TabsBlock } from './tabs-block';
import * as colorThemes from '../../color-themes';
import { SheetTasksSidebar } from '../sheet-view/sheet-tasks-sidebar';

const Wrapper = styled.div`
    font-family: sans-serif;
    height: 100vh;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto 1fr;
    /* grid-row-gap: 1em; */

    /* > * + * {
        margin-top: 1em;
    } */
`;
const TabsListWrapper = styled.div`
    grid-area: 1 / 1 / 2 / 3;
    background-color: ${colorThemes.main.tabsBg};
    padding: 1em;
    padding-bottom: 0;
`;
const SheetHeaderWrapper = styled.div`
    grid-area: 2 / 1 / 3 / 3;
    background-color: ${colorThemes.main.header};
    padding: 1em;
`;
const SheetEditorWrapper = styled.div`
    grid-area: 3 / 1 / 4 / 2;
    overflow: auto;
    padding: 2em;
`;
const SheetTasksListBlockWrapper = styled.div`
    grid-area: 3 / 2 / 4 / 3;
    background-color: ${colorThemes.main.sidebar};
    overflow: auto;
    padding: 1em;
`;

export const MainView: React.FC<{store: Store}> = observer(({store}) => {
    React.useEffect(() => {
        // store.loadStorage();
        loadLocalStorageData();
    }, []);

    async function loadLocalStorageData() {
        store.localStorageAdapter.createIfDoesNotExist(['tabs']);

        const data = await store.localStorageDB.read();

        if (data.tabs.length) {
            store.setSheets(data.tabs);
        }
        else {
            store.addEmptySheet();
        }
    }

    return (
        <Wrapper>
            <TabsListWrapper>
                <TabsBlock store={store} />
            </TabsListWrapper>

            {
                store.activeSheet
                &&
                <>
                    <SheetHeaderWrapper>
                        <SheetHeader store={store} sheet={store.activeSheet} />
                    </SheetHeaderWrapper>

                    {/* <SheetViewWrapper>
                        <SheetView store={store} sheet={store.activeSheet} />
                    </SheetViewWrapper> */}

                    <SheetEditorWrapper>
                        <SheetEditor store={store} sheet={store.activeSheet} />
                    </SheetEditorWrapper>

                    <SheetTasksListBlockWrapper>
                        <SheetTasksSidebar store={store} />
                    </SheetTasksListBlockWrapper>
                </>
            }
        </Wrapper>
    );
});