import * as React from 'react';
import { action, makeObservable, observable } from 'mobx';
import { observer, useObserver } from "mobx-react-lite";
import styled from '@emotion/styled';

import { Store, Sheet } from '../../../store/store';
import { SheetStatsBlock } from './sheet-stats-block';
import { SheetActionsBlock } from './sheet-actions-block';

const Wrapper = styled.div`
    /* color: hsla(0, 100%, 100%, .9); */
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

export const SheetHeader: React.FunctionComponent<{ store: Store, sheet: Sheet }> = observer(({ store, sheet }) => {
    return (
        <Wrapper>
            <SheetStatsBlock store={store} sheet={sheet} />

            <SheetActionsBlock store={store} sheet={sheet} />
        </Wrapper>
    )
});