import * as React from 'react';
import { action } from 'mobx';
import { observer } from "mobx-react-lite";
import styled from '@emotion/styled';

import { Store, Sheet } from '../../store/store';
import { SheetLinesCollection } from '../../store/sheet-lines-collection';

const Wrapper = styled.div`

`;
const TasksSideListBlock = styled.div`
    font-family: 'Courier Prime', monospace;
    font-size: 14px;

    a {
        color: inherit;
        /* background-color: hsl(240, 66%, 95%); */
    }
`;

export const SheetTasksListBlock: React.FunctionComponent<{ store: Store, sheetLinesCollection: SheetLinesCollection }> = observer(({ store, sheetLinesCollection }) => {
    return (
        <Wrapper>
            <TasksSideListBlock>
                <table>
                    <thead></thead>
                    <tbody>
                        {
                            sheetLinesCollection.startedTaskLines.map((l, i) => {
                                const taskName = (tName: string) => {
                                    let res = tName.replace(/https?:\/\/(www\.)?/, '');
                                    const n = 40;
                                    if (res.length > n) res = `${res.slice(0, n)}...`;

                                    return res;
                                };
                                const taskNameHtml = l.taskName.replace(/https?:\/\/\S+/, m => `<a href="${m}">${taskName(m)}</a>`)

                                return (
                                    <tr key={i}>
                                        <td style={{ textAlign: 'right', color: l.delayColor }}>{(l.dateDiff && l.dateDiff > 0) ? '+' : ''}{l.dateDiff}</td>
                                        <td><span style={{ color: l.taskColor }} dangerouslySetInnerHTML={{ __html: taskNameHtml }}></span></td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </TasksSideListBlock>
        </Wrapper>
    );
});