import * as React from 'react';
import { action, makeObservable, observable } from 'mobx';
import { observer, useObserver } from "mobx-react-lite";
import styled from '@emotion/styled';

import { Store, Sheet } from '../../../store/store';

const Wrapper = styled.div`
    color: hsla(0, 0%, 0%, .66);
    font-size: .9em;
    line-height: 1.5;

    b {
        /* color: hsla(0, 0%, 0%, .8); */
        font-weight: bold;
    }
`;
const TasksStatsBlock = styled.div`
    /* font-size: 1.1em; */
    font-weight: bold;
`;

export const SheetStatsBlock: React.FunctionComponent<{ store: Store, sheet: Sheet }> = observer(({ store, sheet }) => {
    const colors = {
        started: `forestgreen`,
        unstarted: `dodgerblue`,
        overdue: `hsl(0, 50%, 50%)`,
        longOverdue: `hsl(300, 50%, 50%)`,
    };
    const {
        startedTaskLines,
        unstartedTaskLines,
        overdueTaskLines,
        longOverdueTasks,
        //
        totalOverdueDays,
        overdueMean,
        overdueMedian,
        overdueMode
    } = sheet.sheetLinesCollection;

    return (
        <Wrapper>
            <TasksStatsBlock>
                {/* <span>started tasks: <b>{sheet.sheetLinesCollection.startedTaskLines.length}</b> | unstarted tasks: <b>{sheet.sheetLinesCollection.unstartedTaskLines.length}</b> | overdue tasks: <b>{sheet.sheetLinesCollection.overdueTaskLines.length}</b> | long overdue tasks: <b>{sheet.sheetLinesCollection.longOverdueTasks.length}</b></span> */}
                <span>
                    <span>tasks: </span>
                    <span style={{ color: colors.started }}><b>{startedTaskLines.length}</b> started</span><span>, </span>
                    <span style={{ color: colors.unstarted }}><b>{unstartedTaskLines.length}</b> unstarted</span><span>, </span>
                    <span style={{ color: colors.overdue }}><b>{overdueTaskLines.length}</b> overdue</span><span> (</span>
                    <span style={{ color: colors.longOverdue }}><b>{longOverdueTasks.length}</b> long overdue</span><span>)</span>
                </span>
            </TasksStatsBlock>

            <div>
                {
                    totalOverdueDays == 0
                        ?
                        <span>overdue days: <b>{totalOverdueDays}</b></span>
                        :
                        <span>overdue days: <b>{totalOverdueDays}</b> total, <b>{overdueMean.toFixed(1)}</b> average, <b>{overdueMedian}</b> median, <b>{overdueMode.value}</b> dominant ({overdueMode.occurrences} days)</span>
                }
            </div>
        </Wrapper>
    )
});