export class SheetLinesCollection {
    lines: SheetLine[] = [];
    // maxDelay: number = -Infinity;

    // readonly stats: SheetStatsData[] = [];

    constructor() {
        makeObservable(this, {
            lines: observable,

            unstartedTaskLines: computed,
            startedTaskLines: computed,
            totalOverdueDays: computed,
            transformedFileContent: computed,

            longOverdueTasks: computed,
        });
    }

    public setSheetLines(lines: SheetLineOpts[]) {
        this.lines = lines.map(line => {
            return new SheetLine(this, line);
        });
    }

    public addSheetLine(line: SheetLineOpts) {
        this.lines.push(new SheetLine(this, line));
    }

    private get taskLines() {
        return this.lines.filter(l => l.taskName && l.taskName.trim().indexOf('#') !== 0);
    }

    public get unstartedTaskLines() {
        return this.taskLines.filter(l => l.taskName && l.intervalDays && !l.date);
    }

    public get startedTaskLines() {
        return this.taskLines.filter(l => l.taskName && l.date).sort((a, b) => (b.dateDiff || 0) - (a.dateDiff || 0));
    }

    public get overdueTaskLines() {
        return this.startedTaskLines.filter(l => {
            return l.dateDiff && l.dateDiff > 0;
        });
    }

    public get longOverdueTasks() {
        return this.overdueTaskLines.filter(l => {
            return l.dateDiff && l.intervalDays && (l.dateDiff >= l.intervalDays);
        });
    }

    public get totalOverdueDays() {
        return this.overdueTaskLines.reduce((memo, task) => {
            return memo + (task.dateDiff || 0);
        }, 0);
    }

    public get overdueMean() {
        return this.totalOverdueDays / this.overdueTaskLines.length;
    }

    public get overdueMedian() {
        // console.log('this.overdueTaskLines', this.overdueTaskLines.map(l => l.dateDiff))

        return this.overdueTaskLines[Math.floor(this.overdueTaskLines.length / 2)]?.dateDiff;
    }

    public get overdueMode() {
        const values = this.overdueTaskLines.map(task => task.dateDiff) as number[];

        type ValOccurrence = { value: number; occurrences: number };
        type DistResult = [{ [val: number]: number }, ValOccurrence, ValOccurrence];

        const [dist, min, max] = values.reduce((memo: DistResult, val) => {
            const distObj = memo[0];

            if (!distObj[val]) distObj[val] = 0;
            distObj[val] += 1;

            const newDistVal = distObj[val];

            if (newDistVal < memo[1].occurrences) {
                memo[1].value = val;
                memo[1].occurrences = newDistVal;
            }
            if (newDistVal > memo[2].occurrences) {
                memo[2].value = val;
                memo[2].occurrences = newDistVal;
            }

            return memo;
        }, [{}, { value: Infinity, occurrences: -1 }, { value: -Infinity, occurrences: -1 }]);
        // console.log('dist', dist)

        return max;
    }

    public get maxDelay(): number {
        let max = -Infinity;

        for (const line of this.lines) {
            if (line.dateDiff && line.dateDiff > max) max = line.dateDiff;
        }

        return max;
    }

    get transformedFileContent() {
        return this.lines.map(l => {
            const { intervalDays } = l;

            let lineContent = '';

            if (l.isComment) {
                lineContent += `<span style="color: #aaa">${l.input}</span>`;
            }
            else if (l.isSeparator) {
                lineContent += `<span style="background-color: rgba(0, 0, 0, .1); display: inline-block; width: 100%">${l.input}</span>`;
            }
            else if (intervalDays === undefined) {
                lineContent += l.input;
            }
            else {

                // @todo highlighted & clickable links
                // const taskName = l.taskName.replace(/https?:\/\/\S+/g, (str) => {
                //     return `<a href="${str}">${str}</a>`;
                // });

                lineContent += `<span style="color: ${l.taskColor}">${l.taskName}</span><span>${l.intervalStr}</span>${!l.date ? '' : `<span style="color: navy">${l.date}</span>`}`;
            }

            return `<span style="white-space: pre;">${lineContent}</span><br />`;
        }).join('\n');
    }
}