import * as React from 'react';
import { action, makeObservable, observable } from 'mobx';
import { observer, useObserver } from "mobx-react-lite";
import styled from '@emotion/styled';

import { Store, Sheet } from '../../../store/store';
import * as colorThemes from '../../../color-themes';

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    font-size: .9em;

    > * + * {
        margin-left: 1em;
    }
`;
const Input = styled.input`
    background-color: rgba(255, 255, 255, .5);
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    line-height: 1;
    border: 1px solid rgba(0, 0, 0, .25);
    border-right: 0;
    padding: .5em 1em;
    border-radius: .25em 0 0 .25em;
    outline: 0;

    &:focus {
        background-color: rgba(255, 255, 255, .75);
        border-color: dodgerblue;
    }
`;
const Button = styled.button`
    background-color: ${colorThemes.main.tabInactive};
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    line-height: 1;
    border: 2px solid transparent;
    padding: .5em 1em;
    border-radius: .25em;

    &[disabled] {
        background-color: rgba(0, 0, 0, .25);
        color: rgba(0, 0, 0, .66);
    }

    &:not([disabled]) {
        cursor: pointer;

        &:hover {
            background-color: ${colorThemes.main.tabHover};
        }
    }
`;
const ButtonRight = styled(Button)`
    border-radius: 0 .25em .25em 0;
`;

class SheetRenameForm {
    name: string = '';

    constructor(name: string) {
        this.name = name;

        makeObservable(this, {
            name: observable,
            updateName: action,
        });
    }

    updateName(name: string) {
        this.name = name;
    }
}

type InputButtonControlProps = {
    value: string;
    disabled?: boolean;
    onChange: React.ChangeEventHandler<HTMLInputElement>;
};

const InputButtonControl: React.FunctionComponent<InputButtonControlProps> = observer(props => {
    return (
        <>
            <Input
                value={props.value}
                onChange={props.onChange}
            />

            <ButtonRight disabled={props.disabled}>{props.children}</ButtonRight>
        </>
    )
});

export const SheetActionsBlock: React.FunctionComponent<{ store: Store, sheet: Sheet }> = observer(({ store, sheet }) => {
    const [sheetRenameForm] = React.useState(new SheetRenameForm(sheet.name));

    React.useEffect(() => {
        sheetRenameForm.updateName(sheet.name);
    }, [sheet]);

    return (
        <Wrapper>
            <div>
                <form
                    onSubmit={async e => {
                        e.preventDefault();
                        await store.updateSheet(sheet, { name: sheetRenameForm.name });
                    }}
                >
                    <InputButtonControl
                        value={sheetRenameForm.name}
                        disabled={sheetRenameForm.name === '' || sheetRenameForm.name === sheet.name}
                        onChange={async e => {
                            sheetRenameForm.updateName(e.currentTarget.value);
                        }}
                    >Rename</InputButtonControl>
                </form>
            </div>

            <div>
                <Button
                    onClick={() => {
                        if (confirm('are you sure?')) {
                            store.removeSheet(sheet);
                        }
                    }}
                >Delete sheet</Button>
            </div>
        </Wrapper>
    )
});