import * as React from 'react';
import { action } from 'mobx';
import { observer } from "mobx-react-lite";
import styled from '@emotion/styled';

import { Store } from '../../store/store';
import { SheetTasksListBlock } from '../sheet-view/sheet-tasks-list-block';

const Wrapper = styled.div`

`;

export const SheetTasksSidebar: React.FC<{ store: Store }> = observer(({ store }) => {
    if (!store.activeSheet) return null;

    return (
        <Wrapper>
            <div>
                <SheetTasksListBlock store={store} sheetLinesCollection={store.activeSheet.sheetLinesCollection} />
            </div>

            <div>
                {
                    store.activeSheet.categories.slice().sort((a, b) => b.linesCollection.totalOverdueDays - a.linesCollection.totalOverdueDays).map(cat => {
                        return (
                            <div key={cat.name}>
                                <div style={{ marginTop: '1em', fontFamily: `'Courier Prime', monospace`, fontSize: 14 }}>{cat.name || '<unnamed>'}</div>
                                <div style={{ fontFamily: `'Courier Prime', monospace`, fontSize: 12, color: 'grey' }}>total overdue days: {cat.linesCollection.totalOverdueDays}</div>

                                <div>
                                    <SheetTasksListBlock store={store} sheetLinesCollection={cat.linesCollection} />
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </Wrapper>
    );
});