import {Sheet} from './sheet';
import { SheetLinesCollection } from './sheet-lines-collection';

export interface SheetLineOpts {
    input: string;
    // output: string;
    taskName: string;
    intervalStr?: string;
    intervalDays?: number;
    date?: string;
    nextDate?: string;
    dateDiff?: number;
    isComment: boolean;
    isSeparator: boolean;
    isCategoryHeader: boolean;
    isCategoryEndHeader: boolean;
    category: string | null;
}

export class SheetLine {
    input: string;
    // output: string;
    taskName: string;
    intervalStr?: string;
    intervalDays?: number;
    date?: string;
    nextDate?: string;
    dateDiff?: number;
    isComment: boolean;
    isSeparator: boolean;
    isCategoryHeader: boolean;
    isCategoryEndHeader: boolean;
    category: string | null;

    constructor(readonly linesCollection: SheetLinesCollection, opts: SheetLineOpts) {
        this.input = opts.input;
        this.taskName = opts.taskName;
        this.intervalStr = opts.intervalStr;
        this.intervalDays = opts.intervalDays;
        this.date = opts.date;
        this.nextDate = opts.nextDate;
        this.dateDiff = opts.dateDiff;
        this.isComment = opts.isComment;
        this.isSeparator = opts.isSeparator;
        this.isCategoryHeader = opts.isCategoryHeader;
        this.isCategoryEndHeader = opts.isCategoryEndHeader;
        this.category = opts.category;
    }

    get taskColor() {
        const getTaskColor = (date: string | undefined, dateDiff: number | undefined, intervalDays: number | undefined) => {
            if (!intervalDays) return '';
            if (!date || dateDiff === undefined) return 'dodgerblue';

            if (dateDiff == 0) {
                return 'forestgreen';
            }
            else if (dateDiff / intervalDays > 10) {
                return `hsl(285, 100%, 50%)`;
            }
            else if (dateDiff / intervalDays > 4) {
                return `hsl(300, 100%, 50%)`;
            }
            else if (dateDiff / intervalDays > 2) {
                return `hsl(330, 100%, 50%)`;
            }
            else if (dateDiff > 0) {
                const hue = (1 - Math.min(dateDiff / (intervalDays * 2), 1)) * 30;

                return `hsl(${hue}, 100%, 50%)`;
            }
            else if (dateDiff < 0) {
                return '#777';
            }
            else {
                return 'green';
            }
        }
        const taskColor = getTaskColor(this.date, this.dateDiff, this.intervalDays);

        return taskColor;
    }

    get delayColor() {
        if (!this.nextDate || this.dateDiff === undefined) return '';

        return `hsl(0, ${this.dateDiff / this.linesCollection.maxDelay * 100}%, 50%)`
    }
}