import { makeObservable, observable, computed, action } from 'mobx';

import {Sheet, SheetStatsData} from './sheet';
import { Adapters, JsonDB } from '../lib/json-db/json-db';
import { JsonDBInstance } from '../types';
import { JsonDbAdapter } from '../lib/json-db/adapters';

export * from './sheet';

const SHEET_DEFAULT_CONTENT = `workout $d=3 2/06
// not started yet (missing the date)
travel abroad $w=8
// task period not given:
// $w=X for weeks or $d=X for days
some new habit (how often?)

[chores]

shopping $w=1 2/04
house cleaning $w=1 2/03
car washing $w=4 1/19

// digital chores
backup photos $w=4 01/10
update social media $w=1

[side projects]

work on my new app $w=1 02/06
new blog post $w=2 12/24
game project updates $w=1 01/20
homepage updates $w=3 02/02

[hobbies]

practice piano $d=3 02/07
// need to get back to guitar playing
practice guitar $d=3 10/16
practice painting $w=4 12/11`;

export class Store {
    readonly sheets: Sheet[] = [];

    activeSheet: Sheet | null = null;

    constructor(readonly localStorageDB: JsonDBInstance, readonly localStorageAdapter: Adapters.LocalStorage) {
        makeObservable(this, {
            sheets: observable,
            setSheets: action,
            addEmptySheet: action,
            updateSheet: action,
            removeSheet: action,

            activeSheet: observable,
            setActiveSheet: action,
        });

        // this.activeSheet = this.sheets[0];
    }

    setSheets(tabs: {id: number; name: string; content: string}[]) {
        this.sheets.length = 0;

        const sheets = tabs.map(tab => {
            return new Sheet({
                id: tab.id,
                name: tab.name,
                content: tab.content,
            });
        })

        this.sheets.push(...sheets);

        this.setActiveSheet(sheets[0]);
    }

    setActiveSheet(sheet: Sheet) {
        this.activeSheet = sheet;
    }

    async addEmptySheet() {
        const res = await this.localStorageDB.insert('tabs', {name: 'new sheet', content: SHEET_DEFAULT_CONTENT})

        const sheet = new Sheet(res);

        action(() => {
            this.sheets.push(sheet);
        })();

        this.setActiveSheet(sheet);
    }

    async updateSheet(sheet: Sheet, body: {name?: string; content?: string}) {
        const res = await this.localStorageDB.update('tabs', sheet.id, body);

        action(() => {
            sheet.name = res.name;
            sheet.fileContent = res.content;
        })();
    }

    async removeSheet(sheet: Sheet) {
        await this.localStorageDB.delete('tabs', sheet.id)

        const index = this.sheets.indexOf(sheet);
        const isLast = index === this.sheets.length - 1;

        action(() => {
            this.sheets.splice(index, 1);
        })();

        if (this.sheets.length > 0) {
            const newActiveSheetIndex = isLast ? index - 1 : index;

            this.setActiveSheet(this.sheets[newActiveSheetIndex]);
        }
        else {
            await this.addEmptySheet();
        }
    }
}