import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { BrowserRouter, Link, NavLink, Route, Switch } from 'react-router-dom';
import { Global, css } from '@emotion/react';

import * as Store from './store/store';
import { MainView } from './views/main-view/main-view';
import { GridTestView } from './views/grid-test-view/grid-test-view';
import { CtaChartView } from './views/cta-chart-view/cta-chart-view';
import { WritingView } from './views/writing-view/writing-view';
import { TwitterView } from './views/twitter-view/twitter-view';
import { BlogView } from './views/blog-view/blog-view';
import { Adapters, JsonDB } from './lib/json-db/json-db';
import { JsonDbData } from './types';

const localStorageAdapter = new Adapters.LocalStorage({
    name: 'habits_app',
    localStorage: localStorage
});
const localStorageDB = new JsonDB<JsonDbData>({
    adapter: localStorageAdapter,
    hooks: {

    }
});

const store = new Store.Store(localStorageDB, localStorageAdapter);

const cssReset = css`
    html, body {
        margin: 0;
        padding: 0;
    }

    * {
        box-sizing: border-box;
    }
`;

const app = (
    <BrowserRouter>
        <Global styles={cssReset} />

        {/* <ul>
            <NavLink activeStyle={{ fontWeight: 'bold' }} exact to="/">Home</NavLink>
            <NavLink activeStyle={{ fontWeight: 'bold' }} to="/progressions">Progressions</NavLink>
            <NavLink activeStyle={{ fontWeight: 'bold' }} to="/sequences">Sequences</NavLink>
            <NavLink activeStyle={{ fontWeight: 'bold' }} to="/training">Training</NavLink>
        </ul> */}

        <Switch>
            <Route path="/" exact={true}>
                <MainView store={store} />
            </Route>

            {/* <Route path="/cta-chart" exact={true}>
                <CtaChartView store={store} />
            </Route>

            <Route path="/writing" exact={true}>
                <WritingView />
            </Route>

            <Route path="/twitter" exact={true}>
                <TwitterView />
            </Route>

            <Route path="/blog" exact={true}>
                <BlogView />
            </Route>

            <Route path="/grid" exact={true}>
                <GridTestView store={store} />
            </Route> */}

            {/* <Route path="/progressions">
                <ProgressionsView />
            </Route>

            <Route path="/sequences">
                <SequencesView />
            </Route>

            <Route path="/training">
                <TrainingView />
            </Route> */}
        </Switch>
    </BrowserRouter>
);

ReactDOM.render(app, document.getElementById('react-root'));